.block{width:100%;position:relative;padding-top:16px}.block:first-of-type{padding-top:0}.block:last-of-type{padding-bottom:0}.block label{transition:all .25s cubic-bezier(.53,.01,.35,1.5);transform-origin:left center;box-sizing:border-box;color:#98a9bc;padding:10px 12px;display:block;position:absolute;font-size:16px;margin-top:-46px;z-index:2;pointer-events:none}.block .hover{font-size:12px;transform:translateY(-36px)}.input{height:48px;line-height:48px;transition:all .25s cubic-bezier(.53,.01,.35,1.5)}.input:focus~label{font-size:12px;transform:translateY(-36px)}.input :global .ant-input-suffix{display:flex;align-items:center}.pwd{line-height:20px;height:20px;cursor:pointer}